.header {
  padding-inline: 1.4375rem;
  padding-block-start: 1.625rem;
  font-size: 0.875rem;
  background-color: var(--veryPaleBlue-TopBGPattern);
  height: 14.0625rem;
}
@media (min-width: 71.875em) {
  .header {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    padding-inline: 9.375rem;
  }
}
.header__title {
  line-height: 1.5;
  padding-bottom: 0.9375rem;
  border-bottom: 1px solid var(--darkGrayishBlue-Text);
}
@media (min-width: 71.875em) {
  .header__title {
    border-bottom: none;
  }
}
.header__title h1 {
  color: var(--VeryDarkBlue-Text);
  font-size: 1.5625rem;
}
@media (min-width: 71.875em) {
  .header__title h1 {
    font-size: 1.75rem;
  }
}
.header__title span {
  font-weight: 700;
}
.header__toggle {
  border: none;
  padding-block: 1.25rem;
}
@media (min-width: 71.875em) {
  .header__toggle {
    margin-block: 0;
    margin-inline: auto;
    flex-grow: 1;
  }
}

@media (min-width: 71.875em) {
  .cards {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 1.875rem;
  }
}

@media (min-width: 71.875em) {
  main {
    margin-top: -5.625rem;
  }
}

.card {
  padding: 1.5625rem;
  margin-bottom: 1.875rem;
  background-color: var(--lightGrayishBlue-CardBG);
  border-radius: 0.3125rem;
  display: grid;
  grid-template-areas: "image image name name" "num num num num" "today today today today";
  position: relative;
  overflow: hidden;
}
.card:hover {
  transition: background 150ms ease-in-out;
  background-color: var(--light-card-hover);
  cursor: pointer;
}
@media (min-width: 71.875em) {
  .card {
    padding: 1.25rem;
    margin-bottom: 0;
  }
}
.card--facebook {
  margin-top: -2.25rem;
  border-top: 0.4375rem solid var(--facebook);
}
@media (min-width: 71.875em) {
  .card--facebook {
    margin-top: 0;
  }
}
.card--twitter {
  border-top: 0.4375rem solid var(--twitter);
}
.card--instagram::before {
  content: "";
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  width: 100%;
  height: 0.4375rem;
  background-image: linear-gradient(to right, var(--instagram-LG-start) 0%, var(--instagram-LG-end) 60%);
}
.card--youtube {
  border-top: 0.4375rem solid var(--youTube);
}
.card__platform {
  grid-area: image;
  justify-self: end;
  align-self: center;
}
.card__platform img {
  vertical-align: middle;
}
.card__username {
  grid-area: name;
  justify-self: start;
  align-self: center;
  padding-inline-start: 0.3125rem;
  font-weight: 700;
  font-size: 0.875rem;
}
.card__followers {
  grid-area: num;
  justify-self: center;
  padding-block: 1.25rem;
  text-align: center;
}
.card__followers h2 {
  font-size: 3.125rem;
  font-weight: 700;
  color: var(--VeryDarkBlue-Text);
}
.card__followers span {
  font-size: 0.875rem;
  text-transform: uppercase;
  font-weight: 400;
  letter-spacing: 4px;
}
.card__change {
  grid-area: today;
  text-align: center;
  padding-top: 0.9375rem;
}
.card__change img {
  vertical-align: middle;
}
.card__change span {
  font-size: 0.875rem;
  font-weight: 700;
}
.card__change--up {
  color: var(--limeGreen);
}
.card__change--down {
  color: var(--brightRed);
}

.overview h2 {
  padding-block: 1.25rem 1.875rem;
}
@media (min-width: 71.875em) {
  .overview h2 {
    padding-block: 2.5rem 1.25rem;
  }
}
.overview .card-grid {
  display: grid;
  grid-template-areas: "view view img img" "number number today today";
  gap: 0.9375rem;
}
@media (min-width: 71.875em) {
  .overview .card-grid {
    padding: 1.875rem;
  }
}
.overview .card-grid__subtitle {
  grid-area: view;
  justify-self: start;
  align-self: center;
  font-weight: 700;
}
.overview .card-grid img {
  grid-area: img;
  justify-self: end;
  align-self: center;
}
.overview .card-grid__count--small {
  grid-area: number;
  justify-self: start;
  align-self: center;
  vertical-align: middle;
  font-size: 1.5625rem;
  color: var(--VeryDarkBlue-Text);
}
@media (min-width: 71.875em) {
  .overview .card-grid__count--small {
    align-self: center;
  }
}
.overview .card-grid__change {
  grid-area: today;
  justify-self: end;
  align-self: flex-end;
  font-size: 0.875rem;
  font-weight: 700;
}
@media (min-width: 71.875em) {
  .overview .card-grid__change {
    align-self: center;
  }
}
.overview .card-grid__change img {
  vertical-align: middle;
}
.overview .card-grid__change--up {
  color: var(--limeGreen);
}
.overview .card-grid__change--down {
  color: var(--brightRed);
}

.toggle {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 71.875em) {
  .toggle {
    justify-content: end;
  }
}
.toggle label {
  font-weight: 700;
  cursor: pointer;
}
@media (min-width: 71.875em) {
  .toggle label {
    padding-right: 1.25rem;
  }
}
.toggle .hidden {
  position: absolute;
  top: 0;
  left: -10000px;
  width: 1px;
  height: 1px;
}
.toggle__wrapper {
  position: relative;
  height: 1.5rem;
}
.toggle__wrapper input[type=radio] {
  margin: 0 -0.125rem 0 -0.125rem;
  opacity: 0;
  width: 1.75rem;
  height: 1.5rem;
  cursor: pointer;
}
.toggle__background {
  display: block;
  height: 100%;
  position: absolute;
  width: 100%;
  top: 0;
  border-radius: 0.75rem;
  background-color: var(--toggle);
  pointer-events: none;
}
.toggle .hover {
  background-image: linear-gradient(225deg, var(--Toggle-LG-end) 20%, var(--Toggle-LG-start) 80%);
  transition: background-image 5s ease-in-out;
}
.toggle__button {
  position: absolute;
  left: 0.1875rem;
  top: 0.1875rem;
  right: 100%;
  width: 1.1875rem;
  height: 1.1875rem;
  border-radius: 50%;
  background-color: var(--white-BG);
  transition: all 150ms ease-in-out;
  cursor: pointer;
}
.toggle #lightM:checked ~ .toggle__button {
  left: calc(100% - 22px);
  right: 3px;
}

.dark {
  background-color: var(--veryDarkBlue-BG);
  color: var(--desaturatedBlue-Text);
}
.dark .header {
  background-color: var(--veryDarkBlue-TopBGPattern);
}
.dark .header h1 {
  color: var(--white-Text);
}
.dark .card {
  background-color: var(--darkDesaturatedBlue-CardBG);
}
.dark .card:hover {
  transition: background 150ms ease-in-out;
  background-color: var(--dark-card-hover);
  cursor: pointer;
}
.dark .card h2 {
  color: var(--white-Text);
}
.dark .card h3 {
  color: var(--white-Text);
}
.dark .overview h2 {
  color: var(--white-Text);
}

.darkRadioBG {
  background-image: linear-gradient(225deg, var(--Toggle-LG-end) 20%, var(--Toggle-LG-start) 80%);
}

.darkToogleBtn {
  background-color: var(--darkDesaturatedBlue-CardBG);
}

:root {
  --font-Inter: "Inter", sans-serif;
}

:root {
  /* Primary: */
  --limeGreen: hsl(163, 72%, 41%);
  --brightRed: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-LG-start: hsl(37, 97%, 70%);
  --instagram-LG-end: hsl(329, 70%, 58%);
  --youTube: hsl(348, 97%, 39%);
  /* Dark Theme */
  --veryDarkBlue-BG: hsl(230, 17%, 14%);
  --veryDarkBlue-TopBGPattern: hsl(232, 19%, 15%);
  --darkDesaturatedBlue-CardBG: hsl(228, 28%, 20%);
  --desaturatedBlue-Text: hsl(228, 34%, 66%);
  --white-Text: hsl(0, 0%, 100%);
  --Toggle-LG-start: hsl(210, 78%, 56%);
  --Toggle-LG-end: hsl(146, 68%, 55%);
  --dark-card-hover: hsl(228, 25%, 27%);
  /* Light Theme */
  --toggle: hsl(230, 22%, 74%);
  --white-BG: hsl(0, 0%, 100%);
  --veryPaleBlue-TopBGPattern: hsl(225, 100%, 98%);
  --lightGrayishBlue-CardBG: hsl(227, 47%, 96%);
  --darkGrayishBlue-Text: hsl(228, 12%, 44%);
  --VeryDarkBlue-Text: hsl(230, 17%, 14%);
  --light-card-hover: hsl(228, 33%, 91%);
  /* Transition */
  --transition: all 150ms ease-in-out;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  font-family: var(--font-Inter);
  background-color: var(--white-BG);
  color: var(--darkGrayishBlue-Text);
  cursor: default;
}

.container {
  width: 90%;
  margin: auto;
}
@media (min-width: 71.875em) {
  .container {
    width: 80%;
  }
}