@use "../util" as *;

html {
    font-size: 100%;
    box-sizing: border-box;
}

*, *::before, *::after {
    box-sizing: inherit;
    margin: 0;
    padding: 0;
}

body {
    font-family: var(--font-Inter);
    background-color: var(--white-BG);
    color: var(--darkGrayishBlue-Text);
    cursor: default;
}
